body,
html {
  color: #20180d;
  position: relative;
  height: 100%;
  background-color: #20180d;
}

body {
  background-image: url('../media/books.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  font-family: 'Open Sans', sans-serif;
}

#quote-box {
  width: 700px;
  height: 450px;
  background-color: #dfc097e3;
  text-align: center;

  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#quote-section {
  height: 75%;
}

#text {
  font-family: 'Goudy Bookletter 1911', serif;
  text-align: center;
  padding: 20px;
  padding-top: 40px;
}

#author {
  text-align: right;
  padding: 20px;
}

#new-quote {
  width: 25%;
  font-size: 1em;
  display: inline-block;
  padding: 20px;
  margin: 0 10px;
  transition: all 0.05s ease-in-out;
  background-color: #795a2b;
}

#new-quote:hover {
  transform: scale(1.1);
  transform-origin: 50% 50%;
  cursor: pointer;
}

#tweet-quote {
  width: 25%;
  padding: 20px;
  margin: 0 10px;
  display: inline-block;
  transition: all 0.05s ease-in-out;
  background-color: #795a2b;
  text-decoration: none;
  color: #20180d;
}

#tweet-quote:hover {
  transform: scale(1.1);
  transform-origin: 50% 50%;
}

@media only screen and (max-width: 600px) {
  #quote-box {
    width: 100%;
    height: 100%;
    background-color: #dfc097e3;
    text-align: center;

    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  #quote-section {
    height: 75%;
  }

  #text {
    font-size: 1.2em;
  }

  #author {
    font-size: 0.8em;
  }

  #tweet-quote:hover {
    transform: none;
  }

  #new-quote:hover {
    transform: none;
  }
}
